body {
    margin: 0;
  }
  
  .MainClass {
    width: 100vw;
    height: 100vh;
  }
  
  .app {
    background-color: white;
    width: 100vw;
    height: 100vh;
  }
  
  .NavBarClass {
    display: flex;
    justify-content: center;
    align-items: center;
      background-color: darkred;
  }
  
  .NavTitle h1 {
    display: flex;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    justify-content: flex-start;
    padding-right: 15rem;
    margin-right: 15rem;
    color: white;
    font-size: 38px;
  }
  
  .NavBarClass li {
    margin-left: 3rem;
  }
  
  .NavBarClass li a {
    font-size: 32px;
    font-family: Arial;
    color: white;
  }
  
  li {
    list-style: none;
    padding: 0.3rem;
    margin: 0.3rem;
  }
  
  input {
    width: 200px;
    height: 30px;
  }
  
  a {
    font-size: 18px;
    text-decoration: none;
    color: black;
  }
  

  .Sign {
    padding-left: 0.5rem;
    text-decoration: underline;
    color: peachpuff;
  }
  
  input {
    margin: 0.2rem;
    border-radius: 0.5rem;
  }
  
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .LoginClass {
    display: flex;
    height: 80vh;
    width: 100vw;
    margin-top: 0.5rem;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .LoginClass form {
    display: flex;
    padding: 1rem;
  }
  
  .LoginClass form input {
    display: flex;
    font-size: 18px;
    text-align: center;
    margin-top: 1rem;
    height: 8vh;
    width: 30vw;
  }
  
  input:focus::placeholder {
    color: transparent;
  }
  
  .LoginClass form button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: 0.1rem solid darkred;
    width: 30vw;
  }
  
  .SignUpClass {
    display: flex;
    margin-top: 0.5rem;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  button {
    height: 35px;
    width: 100px;
    font-size: 20px;
    margin-top: 0.5rem;
    border-radius: 0.5rem;
    border: 0.1rem solid black;
  }
  
  .MainPosts h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.2rem;
    font-size: 54px;
  }
  
  .PostInputBar {
    display: block;
    justify-content: center;
    font-size: 28px;
    text-align: center;
    margin: auto;
    width: 18vw;
    height: 3vh;
  }
  
  .PostInput {
    display: block;
    justify-content: center;
    font-size: 28px;
    text-align: center;
    margin: auto;
  }
  
  .SinglePost {
    margin: 1.2rem;
    padding: 0.5rem;
    border: 0.1rem solid black;
    border-radius: 1rem;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  }
  
  .SinglePost a button {
    width: 20vw;
  }
  
  .HomePage {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 34px;
    text-align: center;
  }
  
  .HomePage h1 {
    font-weight: 600;
    margin-bottom: 2rem;
  }
  
  .HomePage h2 {
    font-weight: 500;
    margin: 1rem;
  }
  
  .ProfileButton {
    width: 10vw;
    height: 5vh;
    background-color: white;
    border: 0.1rem solid blue;
    color: blue;
    opacity: 0.8;
  }
  
  
  .CreatePost h1 {
    display: flex;
    justify-content: center;
    font-size: 68px;
    margin-top: 5rem;
  }
  
  .CreatePost input {
    display: flex;
    font-size: 18px;
    text-align: center;
    margin-top: 1rem;
    height: 6vh;
    width: 30vw;
    border: 0.2rem solid blue;
  }
  
  .CreatePost label {
    font-size: 24px;
    margin-top: 1rem;
  }
  
  .CreatePost button {
    width: 30vw;
    height: 4vh;
    border: 0.1rem solid blue;
    background-color: white;
    opacity: 0.7;
  }
  
  .Message {
    margin: 1.2rem;
    padding: 0.5rem;
    border: 0.1rem solid black;
    border-radius: 1rem;
  }
  

  
  .ProfileTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
  }